body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightgray;
  height: 100vh;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.card {
  border: 1px solid cyan;
  border-radius: 10px;
  padding: 35px;
  margin-top: 75px;
  width: 100%;
  max-width: 250px;
  box-shadow: 0 4px 8px rgba(100, 15, 15, 0.2);
  background-color: lavender;
}

.card:hover {
  transform: scale(1.1);
  border: 2px solid blue;
  box-shadow: 0 8px 16px blueviolet;
}

button {
  background-color: orangered;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background-color: darkgoldenrod;
  border: 1.5px solid black;
  transform: scale(1.05);
}

.heading {
  text-align: center;
  padding-bottom: 30px;
  font-size: 3rem;
  color: white;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5),
    /* Light shadow to create depth */ -1px -1px 2px rgba(255, 255, 255, 0.7),
    /* Light highlight for the engraved look */ 2px 2px 4px rgba(0, 0, 0, 0.3); /* Subtle shadow for 3D effect */
  position: relative;
  z-index: 1;
}

.para {
  text-align: center;
  text-shadow: 2cm;
  color: green;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 25px;
  padding: 4px;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5),
    /* Light shadow to create depth */ -1px -1px 2px rgba(255, 255, 255, 0.7),
    2px 2px 4px rgba(0, 0, 0, 0.3); /* Subtle shadow for 3D effect */
  position: relative;
  z-index: 2;
  background-color: aliceblue;
}

@media (max-width: 768px) {
  .card {
    width: 45%;
  }
}

@media (max-width: 480px) {
  .card {
    width: 90%;
  }
}
